body {
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  margin: 0;
  font-family: Museo Sans Cyrl, Montserrat;
}

a {
  color: #333;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

.second__header {
  color: #333;
  width: 100%;
  margin-bottom: 40px;
  font-family: Museo Sans Cyrl;
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: 44px;
  position: relative;
}

.color__wrapper {
  background: var(--background-color);
}

.second__header:after {
  content: "";
  width: 254px;
  height: 50px;
  background: rgba(98, 72, 255, .1);
  position: absolute;
  bottom: 0;
  left: 0;
}

.correct__input {
  color: #828282;
  width: 248px;
  border: 1px solid #0eac00;
  border-radius: 3px;
  margin-bottom: 24px;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
}

.wrong__input {
  color: #828282;
  width: 248px;
  border: 1px solid #eb5757;
  border-radius: 3px;
  margin-bottom: 8px;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
}

.error-invisible {
  color: #eb5757;
  text-align: left;
  font-family: Museo Sans Cyrl;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  display: none;
}

.error-visible {
  color: #eb5757;
  text-align: left;
  align-self: left;
  font-family: Museo Sans Cyrl;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.logotype__text {
  text-transform: uppercase;
  color: var(--dark--text-menu-color);
  font-family: Museo Sans Cyrl;
  font-size: 15px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

.logotype {
  margin-right: 57px;
  display: block;
}

@media screen and (min-width: 768px) {
  .second__header {
    color: #333;
    width: 483px;
    margin-bottom: 56px;
    font-family: Museo Sans Cyrl;
    font-size: 56px;
    font-style: normal;
    font-weight: 500;
    line-height: 66px;
    position: relative;
  }

  .logotype__text {
    display: none;
  }

  .second__header:after {
    content: "";
    width: 350px;
    height: 37px;
    background: rgba(98, 72, 255, .1);
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .wrong__input, .correct__input {
    width: 425px;
  }
}

@media screen and (min-width: 1440px) {
  .second__header {
    color: #333;
    width: 483px;
    margin-bottom: 68px;
    font-family: Museo Sans Cyrl;
    font-size: 56px;
    font-style: normal;
    font-weight: 500;
    line-height: 66px;
    position: relative;
  }

  .logotype__text {
    display: block;
  }

  .second__header:after {
    content: "";
    width: 296px;
    height: 72px;
    background: rgba(98, 72, 255, .1);
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .wrong__input, .correct__input {
    width: 425px;
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
}

@media screen and (min-width: 320px) {
  .container {
    width: 280px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 688px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1200px;
  }
}

.navigation__list {
  display: flex;
}

.navigation {
  display: none;
}

.navigation__link {
  color: #333;
  font-family: Museo Sans Cyrl;
  font-size: 15px;
  font-weight: 500;
  line-height: 16px;
}

.header__container-select {
  display: none;
}

header {
  padding-top: 20px;
  padding-bottom: 20px;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navigation__wrapper {
  align-items: center;
  display: flex;
}

.navigation__item {
  margin-right: 32px;
}

.header .choices__inner {
  width: 100%;
  min-height: 25px;
  color: #333;
  background: #fff;
  border: 1px solid #000;
  border-radius: 60px;
  font-family: Museo Sans Cyrl;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.navigation__buttons {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .choices__list--dropdown, .header .choices__list {
  width: 125%;
}

.header .choices[data-type*="select-one"]:after {
  right: 0;
}

.header .choices__list--dropdown {
  word-break: normal;
}

.header .choices__list--dropdown .choices__list {
  overflow: hidden;
}

.header .choices__list--dropdown .choices__item--selectable.is-highlighted {
  background-color: var(--main-color);
  color: #fff;
}

@media screen and (min-width: 768px) {
  header {
    margin-right: 0;
  }

  .logotype__text, .navigation, .header__container-select {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  .header__wrapper {
    margin-right: -60px;
  }

  .logotype__text {
    display: block;
  }

  .header__container-select {
    margin-right: 44px;
  }

  .navigation, .header__container-select {
    display: block;
  }
}

